<template>
    <!-- <button @click="$store.commit('guan')"> x</button> -->
    <div id=" login" class="content-page" v-show="$store.state.isdenglushow">
        <div style="  max-width: 450px;  margin: 0 auto;  background: white;  border-radius: 16px;
             box-shadow: var(--card-shadow); overflow: hidden; width: 430px;">
            <!-- 头 -->
            <div style="background: var(--primary-color); color: white; text-align: center; padding: 30px">
                <h2 style="font-size: 28px; margin-bottom: 10px">忘记密码</h2>
                <p>志愿社区管家平台</p>
            </div>
            <!-- 填写信息 -->
            <div style="padding: 30px">
                <!-- 电子邮箱 -->
                <div class="form-group">
                    <label for="login-username">电子邮箱</label>
                    <input type="text" id="login-username" class="form-control" placeholder="请输入邮箱" name="email"
                        v-model="email" @input="yhemail" />
                    <span class="yonghumingcuowu">{{ cwemail }}</span>
                </div>
                <!-- 输入新密码 -->
                <div class="form-group">
                    <label for="login-password">输入新密码</label>
                    <input type="password" class="form-control" placeholder="请输入密码" name="Password"
                        v-model="newpassword" @input="yhnewpassword" />
                    <span class="yonghumingcuowu">{{ cwnewpassword }}</span>
                </div>
                <!-- 再次输入 -->
                <div class="form-group">
                    <label for="login-password">再次输入密码</label>
                    <input type="password" id="login-password" class="form-control" placeholder="请输入密码" name="Password"
                        v-model="agrepassword" @blur="yhagrepassword" />
                    <span class="yonghumingcuowu">{{ cwagrepassword }}</span>
                </div>
                <!-- 验证码 -->
                <div class="form-group">
                    <label for="verification-code">验证码</label>
                    <div class="verification-group">
                        <!-- 输入验证码 -->
                        <input type="text" id="verification-code" class="form-control" placeholder="请输入6位验证码"
                            name="verificationCodes" v-model="yanzhengma" @input="shuruyanzhengma()">
                        <!-- 发送验证码 -->
                        <button :disabled="isDisabled" type="button" style="width: 100px;" class="verification-code-btn"
                            id="verification-btn" @click="btnyanzhengma">
                            {{ textverificationCodes }}
                        </button>
                    </div>
                    <span class="yonghumingcuowu" id="confirm-password-error" style="margin-bottom: 10px;">
                        {{ cwyanzhengma }}</span>
                </div>
                <!-- 确认更改 -->
                <button :disabled="isDisabledqs" class="btn btn-primary btn-block" id="loginBtn" type="submit"
                    @click="OkAgrePassword">
                    <span>确定更改</span>
                </button>
                <!-- 返回登录 -->
                <div style="display: flex; justify-content: center; align-items: center; margin-top: 20px;">
                    <label style="margin-left: 5px ;color: var(--primary-color);cursor: pointer;"
                        @click="fanhuidenglu">返回登录</label>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import loadXMLDoc from '@/store/AjaxRequest.js';

export default {
    data() {
        return {
            // 邮箱
            email: "",
            cwemail: "",
            isemail: "",

            // 新密码
            newpassword: "",
            cwnewpassword: "",

            // 再次输入密码
            agrepassword: "",
            cwagrepassword: "",

            //验证码
            yanzhengma: "",
            cwyanzhengma: "",
            isDisabled: false,
            textverificationCodes: "获取验证码",

            // 确定修改
            isDisabledqs: false

        }
    },

    methods: {
        // 验证码按钮
        btnyanzhengma() {
            this.isDisabled = true;
            // 改活3
            let time;
            if (this.isemail) {
                loadXMLDoc({
                    reqBody: { "email": this.email },
                    reqUrl: "http://8.148.31.153:3030/verificationCodes",
                    callback: (error, resData) => {
                        try {
                            const data = JSON.parse(resData)
                            if (!error) {
                                //发送成功
                                this.$store.commit("tanchaung", "验证码已发送，注意查收")
                                this.$store.commit("tanchuangshow", true)

                                let num = 60
                                time = setInterval(() => {
                                    this.textverificationCodes = num
                                    if (num == 0) {
                                        num = 60
                                        clearInterval(time)
                                        time = null
                                        this.textverificationCodes = "获取验证码"
                                        this.isDisabled = false;
                                    }
                                    num--
                                }, 1000);
                                //发送失败
                                if (!data.success) {
                                    this.$store.commit("tanchaung", data.message.msg === "用户已存在" ? "用户已存在" : "发送失败")
                                    this.$store.commit("tanchuangshow", true)
                                    return
                                }
                            }
                        } catch (error) {
                            console.log(error)
                        }
                    }
                })
            } else {
                this.$store.commit("tanchaung", "电子邮件格式不正确")
                this.$store.commit("tanchuangshow", true)
                this.isDisabled = false;
            }

        },

        // 确认更改密码
        OkAgrePassword() {
            // this.isDisabledqs = true
            loadXMLDoc({
                reqBody: {
                    'email': this.email,
                    "verificationCodes": this.yanzhengma,
                    "upPass": this.agrepassword
                },
                reqUrl: "http://8.148.31.153:3030/forgetPass",
                callback: (error, resdata) => {
                    const data = JSON.parse(resdata)
                    try {
                        if (data.success) {
                            this.$store.commit("tanchaung", "修改成功，即将前往登录")
                            this.$store.commit("tanchuangshow", true)
                            this.$router.push({ name: "UserLogin" })
                        } else {
                            this.$store.commit("tanchaung", data.message.msg == "验证码错误,请输入正确验证码" ? "验证码错误" : "修改失败")
                            this.$store.commit("tanchuangshow", true)
                        }
                    } catch (error) {
                        console.log(error)
                    }
                }
            })

        },
        // 电子邮箱
        yhemail() {
            if (this.email === "") {
                this.cwemail = "电子邮件不能为空";
                this.isemail = false
                return;
            }
            if (/\s/.test(this.email)) {
                this.cwemail = "电子邮件不能有空格";
                this.isemail = false
                return;
            }
            if (!/^[a-zA-Z0-9\u4e00-\u9fa5._-]+@[a-zA-Z0-9.-]+\.[a-zA-Z]{2,}$/.test(this.email)) {
                this.cwemail = "电子邮件格式不正确";
                this.isemail = false
                return;
            }
            if (this.email.length > 64) {
                this.cwemail = "电子邮件不应超过64个字符";
                this.isemail = false
                return;
            }
            this.cwemail = "";
            this.isemail = true
        },

        // 新密码
        yhnewpassword() {
            /^.{8,16}$/.test(this.newpassword)
                ? this.cwnewpassword = ""
                : this.newpassword === ""
                    ? (this.cwnewpassword = "密码不能为空")
                    : (this.cwnewpassword = "密码应为8~16个字符");
        },

        // 二次密码
        yhagrepassword() {
            if (this.newpassword == this.agrepassword) {
                this.cwagrepassword = ""
            } else {
                (this.cwagrepassword = "第二次密码错误");
            }
        },

        // 验证码
        shuruyanzhengma() {
            if (!/^\d*$/.test(this.yanzhengma)) {
                this.yanzhengma = ''
                this.cwyanzhengma = "只能输入数字"
                return
            }
            if (this.yanzhengma.length > 6) {
                this.yanzhengma = ''
                this.cwyanzhengma = "验证码必须是6位数字"
            } else {
                this.cwyanzhengma = "";
            }
        },

        // 返回登录
        fanhuidenglu() {
            this.$router.push({ name: "UserLogin" })
        },
    }

}
</script>
<style scoped>
:root {
    --primary-color: #3498db;
    --primarys-hover: #358ac3;
    --border-color: #e0e0e0;
    --text-color: #333;
    --background-light: #f8f9fa;
    --card-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
    --transition: all 0.3s ease;
}

.yonghumingcuowu {
    display: block;
    user-select: none;
    pointer-events: none;
    caret-color: transparent;
    color: red;
    font-size: 13px;
    margin-top: 6px;
    display: block;
    height: 14px;
}

.content-page {
    width: 100%;
    margin: 0 auto;
}

.register-container {
    max-width: 800px;
    margin: 0 auto;
    background: white;
    border-radius: 16px;
    box-shadow: var(--card-shadow);
    overflow: hidden;
    width: 100%;
}

.page-header {
    background: var(--primary-color);
    color: white;
    text-align: center;
    padding: 30px;
}

.page-header h2 {
    font-size: 28px;
    margin-bottom: 10px;
    font-weight: 600;
}

.page-header p {
    font-size: 16px;
    opacity: 0.9;
}

.form-content {
    padding: 30px;
}

.form-row {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 20px;
}

.form-col {
    flex: 1;
    min-width: 250px;
}

.form-group {
    margin-bottom: 5px;
    position: relative;
}

.form-group label {
    display: block;
    margin-bottom: 8px;
    font-weight: 500;
    font-size: 15px;
    color: #555;
}

.form-control {
    width: 100%;
    padding: 12px 15px;
    border: 1px solid var(--border-color);
    border-radius: 8px;
    font-size: 15px;
    transition: var(--transition);
}

.form-control:focus {
    outline: none;
    border-color: var(--primary-color);
    box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
}

.verification-group {
    display: flex;
    gap: 10px;
}

.verification-group .form-control {
    flex: 1;
}

.verification-code-btn {
    background-color: #f8f9fa;
    border: 1px solid var(--border-color);
    border-radius: 8px;
    padding: 0 15px;
    cursor: pointer;
    color: var(--primary-color);
    font-weight: 500;
    white-space: nowrap;
    transition: var(--transition);
}

.verification-code-btn:hover {
    background-color: #e3f2fd;
}

.agreement-group {
    margin: 13px 0;
    display: flex;
    align-items: flex-start;
}

.agreement-group input {
    margin-top: 3px;
    margin-right: 8px;
}

.agreement-group a {
    color: var(--primary-color);
    text-decoration: none;
}

.agreement-group a:hover {
    text-decoration: underline;
}

.btn {
    display: block;
    width: 100%;
    padding: 13px 20px;
    background-color: var(--primary-color);
    color: white;
    font-size: 16px;
    font-weight: 500;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: var(--transition);
}

.btn:hover {
    background-color: rgb(30, 136, 229, 0.7);
    transform: translateY(-2px);
}

.btn:active {
    transform: translateY(0);
}

.login-link {
    text-align: center;
    margin-top: 25px;
    padding-top: 20px;
    border-top: 1px solid var(--border-color);
    font-size: 15px;
}

.login-link a {
    color: var(--primary-color);
    text-decoration: none;
    font-weight: 500;
    margin-left: 5px;
}

#goToRegister:hover {
    cursor: pointer;
}
</style>